@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@400;500;600&family=Cormorant+Garamond:ital,wght@0,400;0,500;0,600;1,700&family=Dancing+Script:wght@400;500;600;700&family=Montserrat:wght@400;500;600;700;800;900&family=Plus+Jakarta+Sans:wght@400;500;600;700;800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

[data-scroll-container] {
	position: relative;
	overflow: hidden;
	width: 100%;
	height: 100vh;
  }
  
  [data-scroll-section] {
	position: relative;
	overflow: hidden;
	will-change: transform;
  }

  * {
	scrollbar-width: none; /* Firefox */
	-ms-overflow-style: none;  /* Internet Explorer 10+ */
  }
  
  *::-webkit-scrollbar { 
	display: none;  /* Safari and Chrome */
  }
  
  :root {
	--offset: 0px;
	--move-initial: calc(-25% + var(--offset));
	--move-final: calc(-50% + var(--offset));
}
